body {
  width: 100%;
  line-height: 1.5;
  letter-spacing: 0.01em;
  box-sizing: border-box;
  margin: 0;
  background-color: #ffffff;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #CDCDCD;
  background-color: #ffffff;
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

* {
  font-weight: 400;
  font-style: normal;
  padding: 0;
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }

@media (max-width: 768px) {
  body {
    max-width: 768px; } }

@media (max-width: 320px) {
  body {
    max-width: 320px; } }

.page-header {
  width: 73%;
  display: flex;
  padding-top: 23px;
  margin-left: auto;
  margin-right: auto;
  background-color: #1B2D37;
  justify-content: space-between;
  box-sizing: border-box; }

.page-header__icon {
  width: 202px;
  height: 52px; }

.page-header__picture {
  width: 100%;
  height: 52px; }

.page-header__burger {
  display: none; }

@media (max-width: 768px) {
  .page-header__icon-letter {
    width: 25.33px;
    height: 18.67px; }
  .page-header__icon-phone {
    width: 24px;
    height: 24px; } }

@media (max-width: 320px) {
  .page-header {
    height: 38px;
    width: 38px; } }

.wrapper {
  background-color: #1B2D37; }

@media (max-width: 768px) {
  .wrapper {
    margin: 0;
    max-width: 768px; } }

@media (max-width: 320px) {
  .wrapper {
    max-width: 320px; } }

.btn {
  border: 1px solid #ffffff;
  border-radius: 5px;
  background-color: inherit; }

.nav__contacts {
  display: flex;
  justify-content: space-between; }

.nav__btn {
  width: 180px;
  height: 45px;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #E5E5E5; }

@media (max-width: 768px) {
  .nav__burger {
    display: none; } }

.link {
  text-decoration: none;
  color: #ffffff;
  font-family: "PT Mono", monospace;
  font-size: 16px;
  line-height: 18px; }
